/*pricing*/
#pricing {
    color: #eeeeee;
    /*margin-top: -40px;*/
    /*padding-top: 40px;*/
}


.section-title-light {
    font-family: 'Anton', sans-serif;
    font-size: 3.5rem;
}

.slidecontainer {
    width: 100%; /* Width of the outside container */
}

.slider {
    -webkit-appearance: none;
    width: 100%;
    height: 15px;
    display: inline-block;
    border-radius: 5px;
    background: #defcfc;
    outline: none;
    opacity: 0.7;
    -webkit-transition: .2s;
    -webkit-transition: opacity .2s;
    transition: opacity .2s;
}


.slider:hover {
    opacity: 1;
}

.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: var(--bright-red);
    cursor: pointer;
}

.col-centered {
    float: none;
    margin: 0 auto;
}

