    font-size: 1.6rem;
    font-family: 'Oswald', serif;
}

.resultText {
    font-size: 2.5rem;
    font-family: 'Oswald', serif;
    /*color:#f73859;*/
    color: var(--bright-red);
}

.detail-pricing-link {
    cursor: pointer;
    /*color:#ff2e63;*/
    color: var(--bright-red);
    font-style: italic;
    -webkit-transition: cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
}

.detail-pricing-link:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);

}

